:root {
  --text-color: black;
  --background-color: #fff;
  --border-color: blue;
  --hover-border-color: green;
  --lighter-bg-color: #eee;
}

@media (prefers-color-scheme: dark) {
  :root {
    --text-color: white;
    --background-color: #000;
    --border-color: gray;
    --hover-border-color: green;
    --lighter-bg-color: #111;
  }
  .deer {
    filter: invert(1);
  }
}

body {
  // a modified approach to sans-serif fonts
  // font-family: Inter, "SF Compact", "Segoe UI", Roboto, sans-serif;
  font-family: "p22-mackinac-pro", serif;
  font-size: 20px;
  line-height: 2.2em;
  color: var(--text-color);
  background-color: var(--background-color);
}
.hidden {
  display: none;
}
container {
  max-width: calc(100vw - 20%);
  width: 580px;
  margin: auto;
  display: block;
  padding-top: 24px;
  line-height: 2.4rem;
}
h2 {
  margin: 48px auto 0;
  padding: 0;
  line-height: 20px;
}
ul {
  padding: 0;
}
li {
  list-style-type: none;
  padding: 0;
  margin: 24px auto;
}
li > a {
  text-decoration: none;
  display: flex;
  flex-direction: column;
}
.date {
  font-size: 12px;
  opacity: 0.7;
  font-feature-settings: "tnum";
}
.button {
  border: 1px solid var(--border-color);
  padding: 4px 12px;
  border-radius: 20px;
  float: right;
  cursor: pointer;
  text-decoration: none;
  &:hover {
    border-color: green;
    transition: all;
    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
    transition-duration: 200ms;
  }
}
.deer {
  margin-bottom: -4px;
}
.projects {
  ul {
    // grid with 3 columns
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 12px;
    a {
      border: 2px solid var(--border-color);
      border-radius: 12px;
      color: var(--text-color);
      padding: 0.6rem 1rem;
      width: calc(100% - 2rem);
      text-decoration: none;
      &:hover {
        cursor: pointer;
        transition: all 0.2s ease-in-out;
        border-color: green;
      }
      p {
        margin: 0;
        padding: 12px 0;
        font-size: 1rem;
        line-height: 1.6rem;
      }
    }
  }
}
